import { useNavigate } from "react-router-dom";
const Home = () => {
    const navigate = useNavigate();
    const changePage = (index) => {
        var path = "/" + index;
        navigate(path);
    };


    return (
        <div className="Page">
            <center>
                <div class="button">
                    <button onClick={() => changePage("Quiz")}></button>
                    <p></p>
                    <button onClick={() => changePage("SetDatabase")}>DB</button>
                    <p></p>
                </div>
            </center>

        </div>
    );
};

export default Home;
